import { Container, Stepper,StepLabel,Step, Grid, InputBase } from "@mui/material"
import { useState } from "react";
import { Footer, Header } from "../components"
import ButtonSquare from "../components/ButtonSquare"
import ButtonSquareLarge from "../components/ButtonSquareLarge";
import SignUpForm from "../components/SignUpForm";
import StepOne from "../img/step1.svg"
import StepTwo from "../img/step2.svg"
import StepThr from "../img/step3.svg"
import StepFor from "../img/step4.svg"


interface SignUpProps {    
    
}

const SignUp: React.FC<SignUpProps> = (props) => {
  const [step,setStep] = useState(1);
  let imgComponent = null;
  let component = null;
  const clickEvnet = (no:number) => {
    setStep(no);
  };
  const linkSignIn = () => {
    window.location.href = "/signin";
  };

  switch(step){
    case 1:
      imgComponent = <img src={StepOne}/>
      component =   (<>
                    <ButtonSquareLarge background="#FF9100">.</ButtonSquareLarge>  
                    <ButtonSquareLarge background="#00CDB6">.</ButtonSquareLarge>  
                    <ButtonSquareLarge onClick={() =>clickEvnet(2)}></ButtonSquareLarge>  
                    <ButtonSquareLarge onClick={() =>linkSignIn()}></ButtonSquareLarge></>
                )
      break;
      case 2:
      imgComponent = <img src={StepTwo}/>
      component =   (<><ButtonSquareLarge background="#FF9100">/.</ButtonSquareLarge>  
                    <ButtonSquareLarge background="#00CDB6">.</ButtonSquareLarge>  
                    <ButtonSquareLarge onClick={() =>clickEvnet(3)}></ButtonSquareLarge>  
                    <ButtonSquareLarge onClick={() =>clickEvnet(1)}></ButtonSquareLarge></>
                )
      break;
      case 3:
        imgComponent = <img src={StepThr}/>
        component =   (<> 
                    <SignUpForm></SignUpForm>
                    <ButtonSquareLarge onClick={() =>clickEvnet(4)}></ButtonSquareLarge>  
                    <ButtonSquareLarge onClick={() =>clickEvnet(2)}></ButtonSquareLarge></>
                )
      break;
      case 4:
        imgComponent = (<><img src={StepFor}/>
          <span className="font-bold-s17-c573300" style={{textAlign:"center"}}>
             !<br/>
            </span>
          <span className="font-normal-s17-c573300" style={{textAlign:"center",marginLeft:35,marginRight:35}}>
                !<br/>
                    <br/>
                    .</span></>)
        component =   (<> 
                    <ButtonSquareLarge onClick={() =>linkSignIn()}></ButtonSquareLarge> </>
                )
      break;
  }
    return (
        <div className="common-bg">
            <Header 
                    className="" 
                    goodroad="/asset/main/icon-goodroad.png"
                    bell="/asset/main/icon-bell.png"
                    kakao="/asset/main/icon-kakao.png"
                    color="font-bold-s12-c573300"
            />
            <div className="common-wrapper"> 
                <Container></Container>
                <Container>
{/* ######################### Content Area Begin ######################### */}
              <div className="common-blank-50"></div> 
                <div className="common-row-wrap-hcenter-vstretch">
                  <div className="sign-container">
                      <div className="common-blank-50"></div> 
                      <div className="common-flex-column-wrap-hcenter-vcenter">
                          <span className="sign-span-good-load">GOOD ROAD</span>
                          {imgComponent}
                      </div>
                      {component}
                  </div>
                </div>
                <div className="common-blank-50"></div> 
                 
{/* ######################### Content Area End   ######################### */}
                </Container>
                <Container></Container>
                <Footer></Footer>
            </div>
        </div>
    )

}

export default  SignUp